import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Layout, Header, Navigation, Content, Drawer } from 'react-mdl';
import './App.css';
import './css/home.css';
import './css/resume.css';
import './css/contact.css';
import Main from './components/main';
import img from './images/header-background.jpg'

class App extends Component {

	//Função para esconder o Drawer
	hideToggle() {
		var selectorId = document.querySelector('.mdl-layout');
		selectorId.MaterialLayout.toggleDrawer();
	}
	render() {
		return (
			<div className="demo-big-content" style={{ heigh: '100%' }}>
				<Layout style={{ background: `url(${img}) center / cover` }}>
					<Header transparent className="header-color" title=" " scroll>
						<div className="link">
							<Navigation>
								<Link style={{ fontWeight: 'bold' }} to="/">Home</Link>
								<Link className="link" to="/curriculo">Currículo</Link>
								<Link className="link" to="/contato">Contato</Link>
								<Link className="link" to="/portfolio">Portfólio</Link>
								{/* <Link  to="/sobremim">Sobre mim</Link> */}
							</Navigation>
						</div>
					</Header>

					<Drawer title={<Link style={{ textDecoration: 'none', color: 'black', fontFamily: 'Anton' }} to="/">Meu Portfólio</Link>} >
						<Navigation className="menu-h">
							<Link onClick={() => this.hideToggle()} to="/" >Home</Link>
							<Link onClick={() => this.hideToggle()} to="/curriculo">Currículo</Link>
							<Link onClick={() => this.hideToggle()} to="/contato">Contato</Link>
							<Link onClick={() => this.hideToggle()} to="/portfolio">Portfólio</Link>
							{/* <Link to="/sobremim">Sobre mim</Link> */}
						</Navigation>
					</Drawer>
					<Content>
						<div className="page-content" />
						<Main />
					</Content>
				</Layout>
			</div>
		);
	}
}
export default App;
