import React, { Component } from 'react';
import {Grid,Cell} from 'react-mdl';
import '../css/resume.css';

class Formation extends Component {
    render(){
        return (
            <Grid>
            <div className="edit-tit">
                <Cell col={3}>
                    <p style ={{fontSize: '1.4em'}}>{this.props.startYear} - {this.props.endYear}</p>
                </Cell>
                <Cell  col={9}>
                    <h4 style={{marginTop: '0px', color: 'white'}}>{this.props.schoolName}</h4>
                    <h5 style={{marginTop: '0px'}}>{this.props.course}</h5>
                    <p  style={{textAlign: 'justify', fontSize: '120%'}}>{this.props.schoolDescription}</p>
                </Cell>
                </div>    
            </Grid>
        )
    }
}

export default Formation;