import React, { Component } from 'react';
import {Grid,Cell} from 'react-mdl';


class Experience extends Component {
    render(){
        return (
            <Grid>
                <Cell col={3}>
                    <p style ={{fontSize: '1.4em', color: 'white'}}>{this.props.startYear} - {this.props.endYear}</p>
                </Cell>
                <Cell col={9}>
                    <h4 style={{marginTop: '0px'}}>{this.props.jobName}</h4>
                    <p  style={{textAlign: 'justify',fontSize: '120%'}}>{this.props.jobDescription}</p>
                </Cell>
            </Grid>
        )
    }
}

export default Experience;